.course_list {
    width: 43%;
    height: 100%;
    background-color: var(--ambient);
    border: 2px solid var(--secondary-2);
    border-top: none;
    border-right: none;
    display: flex;
    flex-direction: column;
}

.course_list_title {
    font-size: 1.4rem;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 0.7rem;
}

.tabs {
    display: flex;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%), 0 1px 2px 0 rgb(0 0 0 / 6%);
}

.tabs_item.active {
    background-color: unset!important;
    box-shadow: none;
}

.tabs_item {
    flex: 1;
    text-align: center;
    font-size: 1.7rem;
    padding: 1rem 0 0.3rem 0;
    cursor: pointer;
    border-bottom: 2px solid var(--secondary-2);
    box-shadow: inset 0px -1px 3px 0px rgb(0 0 0 / 15%), inset 0px -3px 8px rgb(0 0 0 / 5%);
    background: var(--overlay-1);
}

.tabs_item:not(:first-child) {
    border-left: 2px solid var(--secondary-2);
}

.course_list_content {
    flex-grow: 1;
    overflow-y: auto;
}