.like_dislike {
    text-align: right;
    margin: 0.55rem 0;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%), 0 1px 2px 0 rgb(0 0 0 / 6%);
    width: fit-content;
    margin-left: auto;
}

.like, .dislike {
    font-size: 1.65rem;
    line-height: 0;
    border: 1px solid var(--secondary-2);
    border-radius: 4px;
    padding: 0.3rem 0.6rem;
    background-color: var(--ambient);
    font-weight: 500;
}

.like:hover, .dislike:hover {
    background-color: var(--overlay-1);
}

/* .like path:not(:first-child), .dislike path:not(:first-child) {
    stroke-width: 1px;
    stroke: var(--ambient);
} */

.like span, .dislike span {
    font-size: 1rem;
    vertical-align: middle;
}

.like svg, .dislike svg {
    vertical-align: middle;
    margin-right: 4px;
}

.like {
    border-radius: 4px 0 0 4px;
    border-right: 0;
    position: relative;
}

.dislike {
    border-radius: 0 4px 4px 0;
    border-left: 0;
}

.like::after {
    content: "";
    height: 1.5rem;
    border-right: 2px solid var(--secondary-2);
    display: inline-block;
    width: 1px;
    position: absolute;
    right: 0;
}