:root {
  --primary: #fdd946;
  --secondary: #242424;
  --error: #ef4646;
  --alternative: #00000040;
  --modal-bg-color: #00000080;
  --body-bg-color: #242424;
  --google-bg: #056dff;
  --ambient: #e5e5f7;
  --overlay-1: #0000001c;
  --overlay-2: #00000054;
  --secondary-2: #818181;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  color: var(--secondary);
  margin: 0;
  font-family: sans-serif;
}

button {
  color: var(--secondary);
}

#app {
  min-height: inherit;
  transition: left .2s ease-in-out;
  position: relative;
  left: 0;
  overflow: auto;
}

#app:before {
  white-space: pre;
  content: "UNIX" "\a " "cademy";
  color: #0000000d;
  vertical-align: bottom;
  text-transform: uppercase;
  z-index: -1;
  font-size: 10rem;
  font-weight: 700;
  line-height: 7.3rem;
  position: fixed;
  bottom: 0;
}

#app:after {
  z-index: -2;
  content: "";
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(#bababa 1px, transparent 1px), linear-gradient(to right, #bababa 1px, var(--ambient) 1px);
  background-size: 12.5vw 12.5vw;
  position: fixed;
  top: 0;
}

#app:has(#side-window:not([hidden])) {
  left: -2rem !important;
}

#side-window {
  opacity: 0;
  display: none;
}

.side-window-enter {
  opacity: 0;
  right: -20rem;
  display: flex !important;
}

.side-window-enter-active {
  transition: opacity .3s, right .2s ease-in-out;
  right: 0;
  opacity: 1 !important;
  display: flex !important;
}

.side-window-enter-done, .side-window-exit {
  right: 0;
  opacity: 1 !important;
  display: flex !important;
}

.side-window-exit-active {
  transition: opacity .3s, right .2s ease-in-out;
  right: -20rem;
  opacity: 0 !important;
  display: flex !important;
}

.side-window-exit-done {
  right: -20rem;
  opacity: 0 !important;
}

.unselectable {
  -webkit-user-select: none;
  user-select: none;
}

.unselectable img {
  -webkit-user-drag: none;
}

h1 {
  margin-top: 0;
  margin-bottom: .5rem;
  font-weight: 500;
  line-height: 1.2;
}

button {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: pointer;
}

button.btn {
  border: 0;
  border-bottom: 4px solid #0003;
  border-radius: 45px;
  padding: 13.5px 29px;
  font-size: 18px;
  font-weight: 600;
  line-height: 27px;
}

.navbar-title {
  color: #fff;
