  top: 0;
  left: 0;
}

.modal-container > .modal {
  padding: 24px 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-container > .alert {
  max-width: 100%;
