.course_item_title_container {
    display: flex;
    background: var(--ambient);
    padding: 1.75rem 1.5rem 1.15rem;
    border-bottom: 1px solid var(--secondary-2);
}

.course_item_title {
    font-size: 1.5rem;
}

.course_item_percentage {
    font-size: 1.3rem;
    font-weight: 500;
    margin-left: auto;
    position: relative;
    line-height: 25px;
}

.course_item_percentage::before {
    content: "";
    aspect-ratio: 1;
    border: 4px solid var(--overlay-1);
    background-color: #61ffc4;
    border-radius: 50%;
    display: inline-block;
    position: absolute;
    left: -2rem;
    top: 50%;
    translate: 0 -50%;
    width: 1rem;
}

.course_item_list_container {
    display: none;
}

.course_item_list_container[data-active=true] {
    display: block;
}

.course_item_list {
    list-style: none;
}

.course_item_list li {
    display: flex;
    border-bottom: 1px solid var(--alternative);
    border-top: unset;
    padding: 0 1.3rem;
    font-size: 1rem;
    height: 4rem;
    /* line-height: 4rem; */
    position: relative;
    font-weight: 500;
    padding-top: 0.85rem;
    align-items: center;
}

.course_item_type {
    display: inline-block;
}

.course_item_type svg {
    height: 100%;
    display: inline-block;
    vertical-align: middle;
    width: 1.65rem;
    height: 1.65rem;
    margin-right: 1rem;
}

.course_item_est_time, .course_item_done {
    margin-left: auto;
    flex-shrink: 0;
}

.course_item_done {
    font-size: 1.45rem;
}

.course_item_list li[data-inactive=true]{
    color: var(--overlay-2);
}

.course_item_list li[data-live=true]{
    background-color: var(--primary);
    border-radius: 8px;
    margin: 12px 8px;
    outline: 2px solid black;
    box-shadow: inset 0px -3px 4px 4px rgb(0 0 0 / 10%), inset -2px -3px 0px 0px rgb(0 0 0 / 30%);
    padding-top: 0.2rem;
}

.course_item_list li[data-live=true]::after{
    content: "";
    border-bottom: 1px solid var(--alternative);
    right: -8px;
    left: -8px;
    position: absolute;
    bottom: -13px;
    display: block;
}

.course_item_done {

}

.course_item_list li:not([data-live=true]):hover {
    background-color: var(--overlay-1);
    /* border-radius: 8px;
    margin: 12px 8px;
