.course_item_list li[data-ispreview=true] > *:last-child::before  {
    content: "preview";
    background-color: #61ffc4;
    border-radius: 4px;
    padding: 2px 6px;
    color: var(--secondary-2);
    border: 1px solid var(--secondary-2);
    margin-right: 8px;
    font-size: smaller;
    box-shadow: 0 1px 1px 0 rgb(0 0 0 / 20%);
}