.course_desc_file {
    padding: 1.75rem 1.5rem;
}

.course_desc_file p {
    margin: 0.4rem 0 0.5rem;
}

.course_file_card {
    display: flex;
    border: 1px solid var(--secondary-2);
    border-radius: 4px;
    box-shadow: inset 0px -3px 0px #00000017;
    padding: 0.85rem 1.3rem 0.35rem;
    margin: 0.5rem 0 0.7rem;
    width: fit-content;
}

.course_file_icon {
    margin-right: 1rem;
}

.course_file_icon svg {
    vertical-align: middle;
    width: 1.65rem;
    height: 1.65rem;
    display: inline-block;
}