.card_item {
    width: 100%;
    position: relative;
    /* padding: 24px 16px; */
    
    
    
    /* box-shadow: 0 2px 4px rgb(0 0 0 / 8%), 0 4px 12px rgb(0 0 0 / 8%); */
}

.card_item_helper {
    width: 100%;
    border-radius: 8px;
    padding: 1rem 0.5rem 0;
    transition: all .2s ease-in-out;
    overflow: hidden;
    background: var(--primary);
    box-shadow: 0px 0px 0px 2px var(--secondary);

}

.card_item:hover .card_item_helper {
    z-index: 1;
    scale: 1.1;
    /* box-shadow: 0 2px 4px rgb(0 0 0 / 8%), 0 4px 12px rgb(0 0 0 / 8%);   */
    /* position: absolute; */
}

/* .card_item:hover .card_item_image {
    padding-top: 50%;
} */

.card_item_image {
    position: relative;
    padding-top: 60%;
    overflow: hidden;
    /* left: -16px; */
    /* margin-right: -32px; */
    /* top: -24px; */
    /* margin-bottom: -24px; */
    border: 2px solid var(--secondary);
    border-radius: 4px;
}

.card_item_image > img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 130%;
}

.card_item_title {
    margin-top: 12px;
    text-overflow: ellipsis;
    display: -webkit-box;
    overflow: hidden;
    word-wrap: break-word;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    font-size: 1.2rem;
}

.card_item_desc {
    margin-top: 12px;
    font-size: 0.9rem;
    text-overflow: ellipsis;
    display: -webkit-box;
    overflow: hidden;
    word-wrap: break-word;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.card_item_footer {
    margin-top: 24px;
    line-height: 31px;
}

.card_item_footer > * {
    display: inline-block;
}

.card_item_price {
    float: right;
    border: none!important;
    padding: 2px!important;
    background: none!important;
}

.card_item_price_old {
    font-size: 16px;
    border: none;
    padding: 2px!important;
    background: none!important;
    text-decoration: line-through;
    position: relative;
    float: right;
    /* z-index: 1; */
}

.card_item_price_old::before{
    content: "⠀⠀";
    z-index: 1;
    position: relative;
}

.card_item_price_old > span{
    width: 12px!important;
    position: absolute;
    left: 4px;
    top: 11px;
    /* z-index: -1; */
}

.card_item_users {
    color: #0000006b;
    font-size: larger;
}

.card_item_users > svg{
    margin-bottom: -2px;
}