$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
);

:root {
  --primary: #fdd946;
  --secondary: #242424;
  --error: #ef4646;
  --alternative: rgba(0,0,0,0.25);
  --modal-bg-color: #00000080;
  --body-bg-color: #242424;
  --google-bg: hsl(215deg 100% 51%);
  --ambient: #e5e5f7;
  --overlay-1: #0000001c;
  --overlay-2: #00000054;
  --secondary-2: #818181;
}

@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);

@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
    margin: 0;
    font-family: sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    // background-color: var(--body-bg-color);
    min-height: 100vh;
    color: var(--secondary);
}

button {
  color: var(--secondary);
}

#app {
  min-height: inherit;
  overflow: auto;
  position: relative;
  left:0;
  transition: left 0.2s ease-in-out;

  &::before {
    white-space: pre;
    content: "UNIX" "\a" "cademy";
    font-size: 10rem;
    position: fixed;
    bottom: 0;
    font-weight: 700;
    color: rgba(0,0,0,0.05);
    vertical-align: bottom;
    text-transform: uppercase;
    line-height: 7.3rem;
    z-index: -1;
  }

  &::after {
    top: 0;
    z-index: -2;
    content: '';
    position: fixed;
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(#bababa 1px, transparent 1px), linear-gradient(to right, #bababa 1px, var(--ambient) 1px);
    background-size: calc(100vw * 0.125) calc(100vw * 0.125);
  }

  &:has(#side-window:not([hidden])) {
    left: -2rem!important;
  }
}

