#side-window {
  display: none;
  opacity: 0;
}

.side-window-enter {
  opacity: 0;
  display: flex!important;
  right: -20rem;
}

.side-window-enter-active {
  opacity: 1!important;
  display: flex!important;
  right: 0;
  transition: opacity 300ms, right 0.2s ease-in-out;
}

.side-window-enter-done {
  opacity: 1!important;
  display: flex!important;
  right: 0;
}

.side-window-exit {
  opacity: 1!important;
  display: flex!important;
  right: 0;
}

.side-window-exit-active {
  opacity: 0!important;
  display: flex!important;
  right: -20rem;
  transition: opacity 300ms, right 0.2s ease-in-out;
}

.side-window-exit-done {
  opacity: 0!important;
  right: -20rem;
}


// [hidden]{
