}

/* DISPLAY */

.d-flex {
  display: flex;
}

.justify-between {
  justify-content: space-between;
}

.grid-view {
  display: grid;
  grid-template-columns: repeat( auto-fill, minmax(13rem, 1fr) );
  gap: 2rem;
}


