.side_window {
    position: absolute;
    right: 0;
    width: 20rem;
    max-width: 100%;
    z-index: 1011;
    background: var(--ambient);
    height: 100%;
    border-left: 2px solid var(--secondary);
    display: flex;
    flex-direction: column;
    position: fixed;
}

.back_button {
    height: 4.3rem;
    line-height: 4.3rem;
    background-color: var(--primary);
    border-bottom: 2px solid var(--secondary);
}

.button_icon {
    padding: 0 0.9rem;
    font-size: 1.5rem;
    height: 3.5rem;
    line-height: 3.5rem;
    cursor: pointer;
}

.button_icon * {
    vertical-align: middle;
}


.button_icon:hover {
    background-color: rgba(0, 0, 0, 0.1);
}

.button_icon svg {
    vertical-align: middle;
}

.button_icon .title {
    font-size: 1.2rem;
    font-weight: 500;
    text-transform: lowercase;
    margin-left: 1rem;
}

.button_icon:first-child{
    margin-top: 1.2rem;
}

.content_scroll {
    overflow-y: auto;
    flex: 1;
    background: var(--ambient);
}

.back {
    font-size: 2.5rem;
    padding: 0 0.5rem;
    height: inherit;
    line-height: inherit;
    display: inline-block;
    cursor: pointer;
}

.back:hover {
    background-color: var(--overlay-1);
}

.back svg {
    vertical-align: middle;
}

.side_window_bg {
    background-color: var(--modal-bg-color);
    position: fixed;
    width: 100vw;
    height: 100vh;
    left: 0;
    z-index: -1;
}