.navbar {
    background: var(--secondary);
    height: 4.3rem;
    padding: 0.4rem 1rem;
    position: relative;
    z-index: 1010;
    display: flex;
    align-items: center;
    border-bottom: 4px solid rgba(0, 0, 0, 0.2);
    color: white;
    position: fixed;
    width: 100%;
    top:0;
}

.navbar > *{
    display: inline-block;
}

.navbar_right {
    margin-left: auto;
    display: inline-block;
}

.navbar_right button {
    border: 0!important;
    border-radius: 4px!important;
    font-size: 1.1rem!important;
    font-weight: 500!important;
    padding: 1rem!important;
    display: inline-block;
    line-height: 0!important;
    /* box-shadow: inset 0px -2px 0px 0px rgb(0 0 0 / 25%), inset 0px 0px 2px 3px rgb(0 0 0 / 10%); */
    position: relative;
    border-bottom: 4px solid #0003!important;
}

.navbar_right button.btn_login {
    margin-right: 8px;
    background: var(--primary);
    /* box-shadow: inset 0 0 0 1px #ffffffc7, inset 0 0 0px 2px var(--secondary-2); */
}

.navbar_right button::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: transparent;
}

.navbar_right button:hover::before {
    background: var(--overlay-1);
}

.profil_picture {
    width: 2.5rem;
    display: inline-block;
    background: #0a0a0a;
    aspect-ratio: 1;
    border-radius: 50%;
    vertical-align: middle;
    border: 3px solid var(--primary);
    margin-right: 0.5rem;
}

.profil_nick {
    vertical-align: middle;
    font-size: 1rem;
    font-weight: 700;
    margin-left: 0.4rem;
}