.course_video {
    padding-top: 56.25%;
    background: #474747;
    position: relative;
}

.course_desc {
    height: 20rem;
    padding: 2rem 2.2rem;
    width: 100%;
}

.course_desc_chapter {
    font-size: 1.5rem;
    margin-bottom: 0.7rem;
}

.course_desc_title {
    font-size: 2rem;
    margin-bottom: 0.7rem;
}

.info_button {
    position: absolute;
    background: none;
    display: block;
    line-height: 0px;
    padding: 0;
    border: 0;
    color: var(--primary);
    fill: var(--secondary);
    top: 1rem;
    right: 1rem;
    font-size: 2rem;
    border-radius: 50%;
    text-align: right;
    outline: none!important;
}

.info_button svg {
    fill: inherit;
}

.info_button:hover .info_button_pop,
.info_button:focus .info_button_pop,
.info_button:focus-visible .info_button_pop {
    display: block!important;
}

.info_button_pop {
    display: none;
    color: var(--ambient);
    background: var(--secondary);
    padding: 4px 8px;
    font-size: .7rem;
    line-height: 17px;
    font-weight: 200;
    margin-top: 8px;
    border-radius: 4px;
    text-align: left;
}

.info_button_pop::before {
    content: "";
    width: 0;
    height: 0;
    border-left: 8px solid #0000;
    border-right: 8px solid #0000;
    border-bottom: 8px solid var(--secondary);
    float: right;
    position: absolute;
    right: 7px;
    margin-top: -11px;
}

.course_desc_creator{
    font-size: 1.1rem;
    flex: 1;
}

.course_desc_length {
    font-size: 1.1rem;
    text-align: right;
}